<template>
    <div class="indexdetails" v-show="hidedetails">
        <mu-popup position="top" :overlay="false" popupClass="demo-popup-top" :open="topPopup">
            网络连接失败
        </mu-popup>
         <mu-appbar :title="title" class="detailsHeader">
            <mu-icon-button icon="arrow_back" slot="left" @click='gobackindex'/>
             <mu-icon-button  slot="right"/>
         </mu-appbar>
         <div v-html="detailsHtml" class="content">  
         </div>
    </div>
</template>
<script>
export default {
  name: 'indexdetails',
  data () {
    return {
      topPopup: false,
      back:'',
      title:'',
      prePath:'',
      preType:'',
      hidedetails:true,
      detailsHtml:''
    }
  },
  mounted () {
     this.getdetailsinfo(this.$route.query.id);
     this.title=this.$route.query.title;
  },
  methods:{
      getdetailsinfo(id){
          this.$http.get("https://cnodejs.org/api/v1/topic/"+id).then(e => {
          this.topPopup=false;
          this.detailsHtml=e.body.data.content;
      }, e => {
        var _this=this;
        this.topPopup=true;
        setTimeout(function() {
           _this.topPopup=false;
        }, 2000);
      })
    },
    gobackindex(){
        window.history.back();
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
.demo-popup-top {
    width: 100%;
    top: 0% !important;
    background: #f06292 !important;
    color: white;
    opacity: .8;
    height: 54px;
    line-height: 54px;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 375px;
    font-size: 20px;
}
.indexdetails .mu-appbar.mu-paper-1{
    height: 48px;
}
.indexdetails  .detailsHeader{
    position: fixed;
    top:0;
}
</style>



